import * as React from 'react'
import { Button, ButtonGroup, Table } from 'pizi-react'
import ReactCalendar from 'react-calendar'
import { useEffect, useState } from 'react'


interface ICalendarProps {
}



function daysInMonth (month: number, year: number) {
    return new Date(year, month, 0).getDate();
}


export const Calendar: React.FunctionComponent<ICalendarProps> = (props) => {
    const [currentDate, setCurrentDate] = useState(new Date())
    
    let data = []
    const month = currentDate.getMonth()
    const day = new Date(currentDate.getTime())
    day.setDate(1)
    day.setDate(2 - day.getDay())

    let line = []
    while(line.length < 7){
        line.push(day.getDate())
        day.setDate(day.getDate() + 1)
        if(line.length === 7) data.push(line)
        if(day.getMonth() <= month && line.length === 7) line = []
    }

  return    <div className="calendar">
                
                <ReactCalendar/>
                <ButtonGroup appearance="simple">
                    <Button iconRight="plus" align="right" onClick={() => setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, currentDate.getDate()))}>Ajouter un vnement</Button>
                </ButtonGroup>
                <div>
                    <Table appearance="simple" header={["LUN", "MAR", "MER", "JEU", "VEN", "SAM", "DIM"]} data={data}></Table>
                </div>
            </div>
}
