import React from 'react';
import './menu.less'
import { ComponentProps, GetComponentClassNames } from '../../../utils/PiziComponent/PiziComponent'
import { ClassNameHelper } from '../../../utils/Utils'
import { IconName } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export interface MenuProps extends ComponentProps{
	title: string
	icon: React.ReactElement
	links: {title: string, link: string, icon: IconName}[]
}

/**
 * Menu UI component
 */
export const Menu: React.FC<MenuProps & React.HTMLAttributes<HTMLDivElement>> = ({
	...props
}) => {
	return 	<header className={ClassNameHelper(GetComponentClassNames("pizi-menu", {...props, appearance: null}))}>
				<div className="icon">{props.icon}</div>
				<div className="title">{props.title}</div>
				<ul className="menu">
					{ props.links.map( link => <li><FontAwesomeIcon icon={link.icon}/>{link.title}</li>)}
				</ul>
	        </header>
}